.svg-symbols {
  border: 0 !important;
  clip: rect(0, 0, 0, 0) !important;
  height: 1px !important;
  margin: -1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  width: 1px !important;
}

[class^='icon-svg-'], [class*=' icon-svg-'] {
  fill: currentColor;
  stroke: inherit;
}

[class^='icon-svg-'] use, [class*=' icon-svg-'] use {
  pointer-events: none;
}

.icon-svg-angle-1-down {
  width: 1.66667em;
  height: 1em;
}

.icon-svg-angle-left {
  width: 0.66631em;
  height: 1em;
}

.icon-svg-buy-mhc {
  width: 1em;
  height: 1em;
}

.icon-svg-envelope {
  width: 1.63517em;
  height: 1em;
}

.icon-svg-key {
  width: 0.938em;
  height: 1em;
}

.icon-svg-logo_mail {
  width: 4.80769em;
  height: 1em;
}

.icon-svg-metaapps {
  width: 1.35771em;
  height: 1em;
}

.icon-svg-metawallet {
  width: 1.17592em;
  height: 1em;
}

.icon-svg-refresh {
  width: 1.29351em;
  height: 1em;
}

.icon-svg-safe {
  width: 0.83378em;
  height: 1em;
}

.icon-svg-settings {
  width: 1em;
  height: 1em;
}

.icon-svg-user {
  width: 1.0661em;
  height: 1em;
}

.btn {
  overflow: hidden;
  text-overflow: ellipsis;
  text-transform: uppercase;
  padding: 18px 20px;
  font-size: 1rem;
  line-height: 1.1875;
  border-radius: 4px;
}

.container-fluid {
  max-width: 1280px;
}

.footer {
  padding: 30px 0 70px;
}

.footer__inner {
  display: flex;
  align-items: flex-start;
}

.footer__logo {
  font-size: 52px;
  margin-left: auto;
  margin-right: 78px;
}

.footer__logo, .footer__logo:hover, .footer__logo:focus {
  color: white;
  text-decoration: none;
}

.footer__logo-img {
  display: block;
}

.form-check {
  font-size: 14px;
  line-height: 16px;
  display: inline-block;
  margin: 0;
}

.form-check-input {
  clip: rect(0, 0, 0, 0);
}

.form-check-input ~ .form-check-label::before {
  content: '';
  width: 18px;
  height: 18px;
  background-clip: content-box;
  padding: 4px;
  border: 1px solid #99CEFF;
  position: absolute;
  top: 7px;
  left: 0;
  transform: translateY(-50%);
  cursor: pointer;
}

.form-check-input:checked ~ .form-check-label::before {
  background-color: #99CEFF;
}

.form-control {
  font-weight: 300;
  font-size: 18px;
  text-transform: uppercase;
  box-shadow: 0 0 10px rgba(153, 206, 255, 0.2);
}

.form-control:focus {
  box-shadow: none;
}

.form-group {
  margin-bottom: 20px;
}

.form-label {
  display: block;
  position: relative;
  margin: 0;
}

.form-label .form-control {
  padding-right: 50px;
}

.form-label__placeholder {
  content: attr(data-label);
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  color: #99CEFF;
  font-weight: 300;
  font-size: 18px;
  line-height: 1.25;
  text-transform: uppercase;
  padding: 20.5px 20px;
  border: 1px solid transparent;
  transition: 0.2s ease-in-out;
  pointer-events: none;
}

.form-control:focus ~ .form-label__placeholder, .form-control--has-value ~ .form-label__placeholder {
  font-size: 12px;
  padding-top: 5px;
  padding-bottom: 5px;
}

.form-label__icon {
  color: #99CEFF;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
  transition: 0.2s ease-in-out;
  pointer-events: none;
}

.form-control:focus ~ .form-label__icon, .form-control--has-value ~ .form-label__icon {
  color: white;
}

.form-label__icon.icon-svg-envelope {
  font-size: 11px;
}

.form-label__icon.icon-svg-safe {
  font-size: 18px;
}

.form-login {
  font-size: 14px;
  line-height: 1.14286;
}

.form-login__header {
  margin-bottom: 20px;
}

.form-login__title {
  font-size: 24px;
  line-height: 1.16667;
}

.form-login__lnk {
  color: #99CEFF;
  text-decoration: none;
  display: inline-block;
  transition: 0.2s ease-in-out;
}

.form-login__lnk:hover, .form-login__lnk:focus {
  color: white;
  text-decoration: none;
}

.form-login__lnk-icon {
  font-size: 16px;
  margin-right: 8px;
  vertical-align: middle;
  transform: translateY(-1px);
}

.form-login__lnk-icon.icon-svg-user {
  font-size: 15px;
}

.form-login__check {
  margin-top: 35px;
  margin-bottom: 35px;
}

.form-login__lnk-continue {
  color: #99CEFF;
  text-decoration: none;
  font-size: 24px;
  line-height: 1.16667;
  transition: 0.2s ease-in-out;
}

.form-login__lnk-continue:hover, .form-login__lnk-continue:focus {
  color: white;
  text-decoration: none;
}

.form-login__btn-sign-in {
  margin-bottom: 44px;
}

.header {
  padding: 19px 10px;
  background: linear-gradient(180deg, #F2F2F2 57.46%, #EBEBEB 100%);
}

.header__inner {
  display: flex;
}

.header__btn {
  color: #636363;
  font-size: 15px;
  line-height: 1;
  border: 0;
  padding: 0;
  background: none;
  width: 32px;
  height: 32px;
  display: flex;
  flex: none;
  justify-content: center;
  align-items: center;
  border-radius: 4px;
  cursor: pointer;
  transition: 0.2s ease-in-out;
}

.header__btn:disabled {
  color: #D9D9D9;
  cursor: default;
}

.header__btn--forward {
  transform: rotateY(180deg);
}

.header__btn--reload {
  font-size: 17px;
}

.header__loc-field {
  color: #636363;
  font-size: 16px;
  border-radius: 4px;
  border: 0;
  padding: 0 40px 0 15px;
  width: 650px;
  height: 32px;
  background: url('data:image/svg+xml;charset=utf-8,%3Csvg%20width%3D%2210%22%20height%3D%226%22%20viewBox%3D%220%200%2010%206%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20xmlns%3Axlink%3D%22http%3A%2F%2Fwww.w3.org%2F1999%2Fxlink%22%3E%3Cuse%20xlink%3Ahref%3D%22%23path0_stroke%22%20transform%3D%22translate(1.04%201.199)%22%20fill%3D%22%238A8A8A%22%2F%3E%3Cdefs%3E%3Cpath%20id%3D%22path0_stroke%22%20d%3D%22M8.31.707A1%201%200%201%200%206.894-.707L8.309.707zM3.8%203.801l-.707.707a1%201%200%200%200%201.414%200L3.8%203.801zM.707-.707A1%201%200%200%200-.707.707L.707-.707zm6.188%200l-3.801%203.8%201.414%201.415L8.31.708%206.895-.708zm-2.387%203.8l-3.8-3.8L-.708.707l3.8%203.801%201.415-1.414z%22%2F%3E%3C%2Fdefs%3E%3C%2Fsvg%3E') no-repeat right 15px center #FFFFFF;
  margin-left: 8px;
  margin-right: 20px;
}

.header__loc-field:focus {
  outline: 0;
}

.header__menu {
  margin-left: auto;
  margin-right: 24px;
}

.menu {
  display: flex;
  overflow: hidden;
}

.menu__item {
  color: #636363;
  text-decoration: none;
  font-size: 16px;
  line-height: 1.25;
  display: flex;
  align-items: center;
  overflow: hidden;
  transition: 0.2s ease-in-out;
}

.menu__item:hover, .menu__item:focus {
  color: #141414;
  text-decoration: none;
}

.menu__item + .menu__item {
  margin-left: 34px;
}

.menu__item-icon {
  flex: none;
  margin-right: 6px;
}

.menu__item-icon.icon-svg-metaapps {
  font-size: 14px;
}

.menu__item-icon.icon-svg-metawallet {
  font-size: 17px;
}

.menu__item-icon.settings {
  font-size: 17px;
}

.menu__item-lbl {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-top: 1px;
}

.page ::selection {
  color: white;
  background-color: #99CEFF;
}

.page__body {
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  min-height: 100vh;
  background: url("../img/promo-spiral.png") no-repeat 0 0, url("../img/team-cloud.png") no-repeat 100% 100% #03081C;
  padding-top: 100px;
}

.page__header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1030;
}

.page__content {
  flex: 1 0 auto;
}

.page__footer {
  flex: none;
}

.title-sect {
  color: white;
  font-size: 60px;
  line-height: 1.16667;
  font-weight: 100;
  margin: 0 0 30px;
}

.title-sect strong {
  font-weight: inherit;
  color: #99CEFF;
}
